import Vue from 'vue'
import Router from 'vue-router'

import Home from '@/views/Home.vue'
import CarNumber from '@/views/CarNumber.vue'

Vue.use(Router)

const router = new Router({
  routes: [
    {
      path: '/',
      name: 'Home',
      component: Home
    },
    {
      path: '/car/number',
      name: 'CarNumber',
      component: CarNumber
    }
  ]
})

export default router
