<template>
  <div class="x-home">
    <el-upload class="upload-demo" multiple action="" :on-change="uploadAction" :auto-upload="false" :file-list="fileList">
      <el-button size="small" type="primary">点击上传</el-button>
    </el-upload>
    <el-divider content-position="left">操作表格 - 按列分组</el-divider>
    <el-button size="small" type="primary" @click="previewTables('preview')">预览表格</el-button>
    <el-input-number v-model="num" label="按第几列分组" size="small" style="margin: 0 20px"></el-input-number>
    <el-button size="small" type="primary" @click="exportTable('one')">导出一个表格</el-button>
    <el-button size="small" type="primary" @click="exportTable('more')">导出多个表格</el-button>
    <el-divider content-position="center">author - wx: angewechat</el-divider>
    <div v-if="index === 'preview'" style="width: 100%;">
      <div style="width: 100%;margin-right: 8px;float:left" :key="'table-' + i" v-for="(table, i) in tables">
        <template v-if="table.list.length > 0">
          <p style="font-size: 12px">共{{table.list.length}}条数据 - {{ table.title }}</p>
          <el-table :data="table.list" height="600px" border stripe size="small">
            <el-table-column type="index" width="50" label="序号" align="center"> </el-table-column>
            <el-table-column v-for="(column, index) in table.cols" :key="'x-' + index" :prop="column.id" :label="column.name"></el-table-column>
          </el-table>
        </template>
        <p v-else>文件解析中...</p>
      </div>
    </div>
    <div v-else-if="index === 'new'" style="width: 100%;">
      <div style="width: 100%;margin-right: 8px;float:left">
        <p style="font-size: 12px">{{ result.title }}</p>
        <el-table :data="result.list" border stripe size="small" height="600px">
          <el-table-column v-for="(column, index) in result.cols" :key="'x-' + index" :prop="column.id" :label="column.name"></el-table-column>
        </el-table>
      </div>
    </div>
  </div>
</template>
<script>
import XLSX from 'xlsx'
export default {
  name: 'Home',
  props: {
    item: {
      type: Object,
      default: function () {
        return {}
      }
    }
  },
  data () {
    return {
      tables: [],
      result: { title: '合并后的表格', list: [], cols: [] },
      fileList: [],
      index: 'xxx',
      num: 8
    }
  },
  methods: {
    previewTables: function (index) {
      this.index = index
      this.tables = []
      for (let index = 0; index < this.fileList.length; index++) {
        const file = this.fileList[index]
        const table = { title: file.name, list: [], cols: [] }
        this.tables.push(table)
        this.processFile(file, table)
      }
    },
    createTable: function (index) {
      this.index = index
      if (this.fileList.length < 3) {
        alert('应该是三个表格')
      }
      for (let i = 0; i < this.tables[2].list.length; i++) {
        const item1 = this.tables[2].list[i]
        let item2 = null
        let item3 = null
        for (let j = 0; j < this.tables[1].list.length; j++) {
          const element = this.tables[1].list[j]
          const keys = Object.keys(element)
          for (let index = 0; index < keys.length; index++) {
            const key = keys[index]
            if (element[key] + '' === item1.column0 + '') {
              item2 = element
            }
          }
        }
        for (let j = 0; j < this.tables[0].list.length; j++) {
          const element = this.tables[0].list[j]
          const keys = Object.keys(element)
          for (let index = 0; index < keys.length; index++) {
            const key = keys[index]
            if (element[key] + '' === item1.column2 + '') {
              item3 = element
            }
          }
        }
        if (item1 && item2 && item3) {
          console.log(item1, item2, item3)
        } else {
          console.log('不合法')
        }
      }
    },
    exportTable: function (type = 'one') {
      const list = this.tables[0].list
      const headerReplace = {}
      for (let index = 0; index < this.tables[0].cols.length; index++) {
        const cols = this.tables[0].cols[index]
        headerReplace[cols.id] = cols.name
      }
      const groups = []
      const key = 'column' + (this.num - 1)
      for (let index = 0; index < list.length; index++) {
        const item = list[index]
        const target = groups.find(t => t.name === item[key])
        if (!target) {
          const group = { name: item[key], list: list.filter(col => col[key] === item[key]) }
          group.list.unshift(headerReplace)
          groups.push(group)
        } else {
          console.log('已存在', item[key])
        }
      }

      if (type === 'one') {
        // 新建book
        const wb = XLSX.utils.book_new()
        for (let index = 0; index < groups.length; index++) {
          const group = groups[index]
          if (group.list.length) {
            console.log('组:', index, group.name)
            const item = group.list[0]
            const keys = Object.keys(item)
            // 新建空workbook，然后加入worksheet
            const ws = XLSX.utils.json_to_sheet(group.list, { skipHeader: true })
            // 设置每列的列宽，10代表10个字符，注意中文占2个字符
            ws['!cols'] = []
            for (let index = 0; index < keys.length; index++) {
              const key = keys[index]
              if (item[key]) {
                ws['!cols'].push({ wch: (item[key] + '').length * 2 + 10 })
              } else {
                ws['!cols'].push({ wch: 10 })
              }
            }
            // 生成xlsx文件(book,sheet数据,sheet命名)
            XLSX.utils.book_append_sheet(wb, ws, group.name)
          }
        }
        // 写文件(book,xlsx文件名称)
        XLSX.writeFile(wb, '分组后.xlsx')
      } else {
        if (confirm('攀岩志提示: 一共将的导出' + groups.length + '个表格, 下载大概需要用时' + (groups.length * 1.5) / 60 + 'min，确定下载？')) {
          // 新建book
          for (let index = 0; index < groups.length; index++) {
            setTimeout((function (index) {
              return function () {
                const group = groups[index]
                if (group.list.length) {
                  console.log('组:', index, group.name)
                  const wb = XLSX.utils.book_new()
                  const item = group.list[0]
                  const keys = Object.keys(item)
                  // 新建空workbook，然后加入worksheet
                  const ws = XLSX.utils.json_to_sheet(group.list, { skipHeader: true })
                  // 设置每列的列宽，10代表10个字符，注意中文占2个字符
                  ws['!cols'] = []
                  for (let index = 0; index < keys.length; index++) {
                    const key = keys[index]
                    if (item[key]) {
                      ws['!cols'].push({ wch: (item[key] + '').length * 2 + 10 })
                    } else {
                      ws['!cols'].push({ wch: 10 })
                    }
                  }
                  // 生成xlsx文件(book,sheet数据,sheet命名)
                  setTimeout(() => {
                    const fileName = group.name && group.name.length > 0 ? group.name : '空分组信息' + index
                    XLSX.utils.book_append_sheet(wb, ws, fileName)
                    XLSX.writeFile(wb, fileName + '.xlsx')
                  }, 5 * 1000)
                }
              }
            }(index)), index * 1000)
          }
          // 写文件(book,xlsx文件名称)
        }
      }
    },
    getGroups: function (arr, key) {
      var map = {}
      arr.forEach(i => {
        // 根据每一项的id进行判断，如果id相同，则应在一个新数组里
        // 如果之前没有以此id为分组的数组
        if (!map[i[key]]) {
          const item = {
            list: [i] // list里存放完整的数据
          }
          item[key] = i[key]
          // 那么创建这个组
          map[i[key]] = item
        } else {
          // 如果已经有这个数组 插入数据
          map[i[key]].list.push(i)
        }
      })

      // 再定义一个空数组进行过滤
      const list = []
      for (var i in map) {
        list.push(map[i])
      }
      return list
    },
    uploadAction: function (file, fileList) {
      console.log(file, fileList)
      this.fileList.push(file)
    },
    processFile (file, table) {
      const reader = new FileReader()
      reader.onload = e => {
        /* 解析数据 */
        const bstr = e.target.result
        const wb = XLSX.read(bstr, { type: 'binary' })
        /* 获取文件的第一个工作表（WorkSheet） */
        const wsname = wb.SheetNames[0]
        const ws = wb.Sheets[wsname]
        table.cols = this.getHeaderRow(ws)
        /* 数组转换 */
        const list = []
        const data = XLSX.utils.sheet_to_json(ws, { header: 1 })
        for (let index = 1; index < data.length; index++) {
          const columns = data[index]
          const item = {}
          for (let i = 0; i < columns.length; i++) {
            item['column' + i] = columns[i] ? columns[i] + '' : ''
          }
          list.push(item)
        }
        table.list = list
        /* 进行表格表头数据更新 */
      }
      const size = file.size / 1024 / 1024
      if (size < 0.5 || confirm('攀岩志提示: 文件有点大，' + size.toFixed(2) + 'M，比较耗时，确认继续么？')) {
        reader.readAsBinaryString(file.raw)
      }
    },
    getHeaderRow: function (sheet) {
      const headers = []
      /* sheet['!ref']表示所有单元格的范围，例如从A1到F8则记录为 A1:F8 */
      const range = XLSX.utils.decode_range(sheet['!ref'])
      let C
      const R = range.s.r /* 从第一行开始 */
      /* 按列进行数据遍历 */
      for (C = range.s.c; C <= range.e.c; ++C) {
        /* 查找第一行中的单元格 */
        const cell = sheet[XLSX.utils.encode_cell({ c: C, r: R })]

        let hdr = 'UNKNOWN ' + C // <-- 进行默认值设置
        if (cell && cell.t) hdr = XLSX.utils.format_cell(cell)
        const item = { id: 'column' + C, name: hdr }
        headers.push(item)
      }
      return headers
    }
  }
}
</script>

<style scoped>
.x-home {
  padding: 20px;
}
</style>
